<template>
    <div class='dashbord'>
        <el-row class='search-bar'>
            <el-col :span='24'>
                <span style='background:#2da8ff'>&nbsp;</span>
                <span style='color:#2da8ff;margin-left:5px'></span>
            </el-col>
        </el-row>

        <el-row class='conference-place'>
            <el-carousel :interval="2500" type="card" height="400px" autoplay=true>

                <el-carousel-item >
                    <div class='blockItem' v-on:click='title="HICSS"'>
                        <conference-block title="HICSS" theme='blue'></conference-block>
                    </div>
                </el-carousel-item>
                
                <el-carousel-item>
                    <div class='blockItem' v-on:click='title="RECYSC"'>
                        <conference-block title="RECYSC" theme='red' start='2016'></conference-block>
                    </div>
                </el-carousel-item>
                
                <el-carousel-item>
                    <div class='blockItem' v-on:click='title="ECIR"'>
                        <conference-block title="ECIR" theme='cyan' start='2017'></conference-block>
                    </div>
                </el-carousel-item>
                
                <el-carousel-item>
                    <div class='blockItem' v-on:click='title="IEEE"'>
                        <conference-block title="IEEE" theme='orange' start='2018'></conference-block>
                    </div>
                </el-carousel-item>
                
                <el-carousel-item>
                    <div class='blockItem' v-on:click='title="ECIR"'>
                        <conference-block title="ECIR" theme='cyan' start='2017'></conference-block>
                    </div>
                </el-carousel-item>
            </el-carousel>
            <!-- <el-col :span='6'>
                <div class='blockItem' v-on:click='title="HICSS"'>
                    <conference-block title="HICSS" theme='blue'></conference-block>
                </div>
            </el-col>

            <el-col :span='6'>
                <div class='blockItem' v-on:click='title="RECYSC"'>
                    <conference-block title="RECYSC" theme='red' start='2016'></conference-block>
                </div>
            </el-col>

            <el-col :span='6'>
                <div class='blockItem' v-on:click='title="ECIR"'>
                    <conference-block title="ECIR" theme='cyan' start='2017'></conference-block>
                </div>
            </el-col>

            <el-col :span='6'>
                <div class='blockItem' v-on:click='title="IEEE"'>
                    <conference-block title="IEEE" theme='orange' start='2018'></conference-block>
                </div>
            </el-col> -->
        </el-row>

        <el-row class='disp-place'>
            <el-col class='disp-left' :span='12'>
                    <div class='title'>{{title}}</div>
                    <div class='intro-place'>{{introduction}}</div>
            </el-col>
            <el-col class='disp-right' :span='12'>
                <!-- <scatter-chart></scatter-chart> -->
                <RadarChart/>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import ConferenceBlock from './components/conferenceBlock.vue'
// import ScatterChart from './components/ScatterChart.vue'
import RadarChart from './components/radarChart.vue'
export default {
    data(){
        return{
            title:'',
        }
    },
    components:{
        ConferenceBlock,
        // ScatterChart,
        RadarChart,
    },
    computed:{
        introduction(){
            let template = '';
            if(this.title=='HICSS'){
                template ="11";
            }
            if(this.title=='RECYSC'){
                template='78910';
            }
            if(this.title=='ECIR'){
                template='1213';
            }
            if(this.title=='IEEE'){
                template='IEEEInstitute of Electrical and Electronics Engineers40160IEEE [1]  90055IEEE [2-3] 2019530IEEE [4]  63 [5] 201996IEEEIEEEAI';
            }
            return template;
        }
    }

}
</script>

<style scoped>
    .dashbord {
        background-color: #f0f3f4;
        width:100%;
        padding:20px 0 20px 20px;
        box-sizing: border-box;
        text-align:left;
    }
    .search-bar{
        /* padding:10px;
        background: #fff;
        margin: 0 -20px 0 -20px;
        font-size:18px; */
        margin-left:0px;
        box-sizing: border-box;
        padding:10px;
        background: #fff;
        margin: 0 -20px 0 0;
        font-size:18px;
        width:100%;
    }
    .search-bar .el-col {
            line-height:40px;
            padding: 0 20px;
            text-align: left;
        }
    .search-bar .el-input{
            width:20%;
            float:right;
            margin-right:10px;
        }
    .search-bar .el-button{
            float:right;
        }

    .conference-place {
        width:100%;
        box-sizing: border-box;
        padding:10px;
        margin: 20px 0;
        
        min-height:400px;
        font-size:18px;
        text-align:left;
        }

        .conference-place .el-col {
                padding: 0 20px;
            }

        .conference-place .el-col .blockItem {
                border-radius: 20px;
                height: 200px;
                
                }
    
    .disp-place{
        margin: 20px 0;
    }
    .disp-place .el-col{
            padding: 0 20px;
            background: #fff;
            height: 600px;
        }
    .disp-place .disp-left{
            box-shadow: inset -30px 0px #f0f3f4;
        }
    .disp-place .disp-right{
            padding:20px;
        }
    .disp-place .title{
            margin-left:-20px;
            padding-top:20px;
            text-align: center;
            font-size:20px;
            font-weight:bold;
        }
    .intro-place{
        font-size:14px;
        padding:20px 30px 0 0;
    }
</style>


