<template>
    <div class="display">
        <h1 class="title"></h1>
        <h2>{{data.title}}</h2>
        <span style="margin-right:20px;font-weight:bold">{{data.src}}</span>
        <span class='abstract'>{{data.time}}</span>
        <p class='abstract'>{{data.src}}</p>
        <span class='abstract' ><span class="abstract-info">12312312</span></span>
    </div>
</template>

<script>
export default {
    props:{
        data:Object
    }
}
</script>

<style  scoped>

    .display {
        text-align: left;
        margin: 20px 20px 0 20px;
    }
     .display .title {
            width: fit-content;
            display: inline-block;
            padding: 2px 12px 4px;
            border-bottom: 4px #2DA8FF solid;
        }

    .display .abstract {
            font-weight: bold;
        }
     .display .abstract-info{
            text-indent: 2em;
            float:left;
        }
</style>


